.github-contri-wrapper{
    height: fit-content;
    width: 93%;
    max-width: 1050px;
    left: 50%;
    position: relative;
    transform: translateX(-50%);
    color: white;
}

.github-row {
    height: 30%;
    width: 100%;
    align-items: center;
    bottom: 1rem;
    position: absolute;
}

.github-contributions{
    margin-top: 20px;
    width: 100%;
    height: 100%;
    opacity: 0.8;
}

.description{
    margin-top: 11rem;
    background: rgba(255, 255, 255, 0.063);
    border-radius: 8px;
    padding: 20px;
    width: fit-content;
    left: 50%;
    transform: translate(-50%);
    position: relative;
    max-height: calc(70vh - 400px);
    overflow-y: hidden;
    transition: opacity 3s ease 0s;
}

.description > div > h2 {
    margin-bottom: 1rem;
}

.description > div > h4 {
    margin-bottom: 2rem;
}

.description > div > p {
    line-height: 1.4rem;
}

.experience-item{
    border-radius: 50%;
    text-align: center;
    position: relative;
    background: white;
    cursor: pointer;
    height: 60px;
    transition: all 1s ease 0s;
}

.row {
    margin-top: 90px;
}

.experience-item > img{
    width: 50px;
    border-radius: 50%;
    margin: 5px;
    transition: all 1s ease 0s;
}

.experience-item-details{
    position: absolute;
    transform: translateX(-50%);
    left: 50%;
    color: white;
    width: 200px;
    margin-top: 10px;
}

@media only screen and (max-width: 600px){
    .experience-item > img {
        width: 30px;
        border-radius: 50%;
        margin: 5px;
        transition: all 1s ease 0s;
    }

    .github-row {
        height: fit-content;
        width: 100%;
        align-items: center;
    }

    .experience-item {
        height: 40px;
    }

    .experience-item-details {
        max-width: 22vw;
    }

    .experience-item-details > h4 {
        font-size: 0.6rem;
    }

    .experience-item-details > h5 {
        font-size: 0.5rem;
    }

    .experience-item-details > p {
        font-size: 0.45rem;
    }

    .description {
        margin-top: 4rem;
        padding: 10px;
        max-height: calc(70vh - 40px);
    }

    .description > div > h2 {
        font-size: 1.4rem;
        margin-top: 4px;
        margin-bottom: 4px;
    }

    .description > div > h4 {
        margin-top: 2px;
        font-size: 1rem;
        margin-bottom: 0;
    }

    .description > div > p {
        font-size: 0.8rem;
        line-height: 1.1rem;
    }

    .row {
        margin-top: 30px;
    }
}